<AppLayout :breadcrumbs="breadcrumbs"><div class="delivery-points"><TransitionFade> <div class="loader" v-if="deliveryPoints.loading" key="load">Загрузка</div><div class="error" v-else-if="deliveryPoints.error" key="error">{{deliveryPoints.error}}</div><div class="empty-list" v-else-if="deliveryPoints.loaded &amp;&amp; deliveryPointListIsEmpty" key="empty">Нет пунктов выдачи</div><DeliveryPointList v-else :deliveryPoints="deliveryPoints.data" key="delivery-points"></DeliveryPointList></TransitionFade></div></AppLayout>